<template>
	<view class="content">
		<view class="mix-list-cell" :class="border" @click="onClick" hover-class="cell-hover"  :hover-stay-time="50" :style="{background:bgClor}">
			<image v-if="image" :src="image" class="cell-icon mix-icon" />
			<text  :class="[point==''?'cell-tit':'clamp',image?'width':'']">{{ title }}</text>
			<text class="cell-tit" v-if="point" style="text-align: center;">{{point}}</text>
			<text v-if="tips" class="cell-tip" :style="{color: tipsColor}">{{ tips }}</text>
			<text class="cell-more mix-icon icon-you" v-if="showRight" ></text>
		</view>
	</view>
</template>
 
<script>
	/**
	 *  简单封装了下， 应用范围比较狭窄，可以在此基础上进行扩展使用
	 *  比如加入image， iconSize可控等
	 */
	export default {
		props: {
			point:{
				type: String,
				default: ''
			},
			image: {
				type: String,
				default: ''
			},
			title: {
				type: String,
				default: '标题'
			},
			tips: {
				type: String,
				default: ''
			},
			tipsColor: {
				type: String,
				default: '#666666'
			},
			navigateType: {
				type: String,
				default: 'right'
			},
			border: {
				type: String,
				default: 'b-b'
			},
			hoverClass: {
				type: String,
				default: 'cell-hover'
			},
			iconColor: {
				type: String,
				default: '#333'
			},
			showRight:{
				type:Boolean,
				default:false
			},
			bgClor:{
				type: String,
				default: '#fff'
			}
		},
		methods: {
			onClick(){
				this.$emit('onClick');
			}
		},
	}
</script>

<style scoped lang='less'>
	.width{
		width:60upx;
	}
	.mix-list-cell{
		display:flex;
		align-items: center;
		height: 96rpx;
		padding: 0 20rpx;
		position:relative;
		&.cell-hover{
			background:#fafafa;
		}
		&.b-b{
			&:after{
				left: 30rpx;
				border-color: #f0f0f0;
			}
		}
		.cell-icon{
			width: 40rpx;
			height:40rpx;
			margin-right: 20upx;
		}
		.cell-more{
			align-self: center;
			font-size: 24rpx;
			color: #999;
			margin-left: 16rpx;
		}
		.cell-tit{
			flex: 1;
			font-size: 28rpx;
			color: #000;
		}
		.clamp{
			font-size: 26rpx;
			color: #333;
			margin-right:10rpx;
		}
	}
</style>
